import React from 'react';
import PropTypes from "prop-types";
function Players({ name, classes, rank }) {
    return (
        <>

            <div className="card" style={{ backgroundColor: "lightGray" }}>
                
                <a href={"https://www.google.com/search?q="+ name}>

                <h3 className="card-title" style = {{backgroundColor:"gray" , color:"wheat", cursor : "pointer"}}> âœ¨{name}</h3>
                </a>


                <div className="card-text">
                    <ul style={{ listStyle: "square" }}>

                        <li style={{ fontSize: "15px" }}>Class: <b>{classes}</b> </li>
                        <li style={{ fontSize: "15px" }}>Ranking: <b>{rank}</b></li>
                    </ul>
                </div>
            </div>

        </>
    )
}

Players.propTypes = {
    name: PropTypes.string.isRequired,
    classes: PropTypes.string.isRequired,
    rank: PropTypes.string.isRequired

}

Players.defaultProps = {
    name: "Default Name",
    classes: "Def. Class",
    rank: "F-"
}

export default Players